/* GLOBAL RESETS */

body {
  padding:0;
  margin:0;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  background-color:black;
  color:white;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}




/* header */

.site-header {
  color:white;
  text-align:left;
  background-color: rgb(27, 27, 27);
  padding:5px;
  height:5em;
  display:-ms-flexbox;
  display:flex;
}

.site-header h1 {
  margin-left:1em;
}

.site-sub-header {
  padding:1em;
  background-color:#FF7F5B;
  line-height:.6em;
  margin-top:1.5em;
  border-radius:15px;
  margin-left:1em;
}

/* entry container */

.entry-container {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack:center;
      justify-content:center;
}


.entry-module {
  background-color:#FF7F5B;
  padding:2em;
  border-radius:10px;
  margin:1.5em;
  width:25em;
  -webkit-box-shadow:2px 2px gray;
          box-shadow:2px 2px gray;
  
}

.entry-module-title {
  font-size:1.1em;
  font-weight:bold;
}

.entry-module-author {
  color:white;
  background-color:black;
  border-radius:2em;
  padding:2px;
  margin:1em;
  width:10em;
  text-align:center;
  -webkit-box-shadow: 2px 2px gray;
          box-shadow: 2px 2px gray;
}

.entry-module-tags {
  font-weight:bold;
}

.open-entry-button {
  padding:8px;
  font-weight:bold;
  width:6em;
  background-color:black;
  color:white;
  border:none;
  -webkit-box-shadow: 2px 2px gray;
          box-shadow: 2px 2px gray;
  display:block;
  margin:0 auto;
  margin-top:2em;
}


